.buttons {
    display: flex;
    flex-wrap: wrap;
}
.buttons__button {
    background-color: transparent;
    color: teal;
    border: none;
    margin: 0 0 0 20px;
    transition: color 0.2s;
}
@media (max-width: 767px) {
    .buttons__button {
        flex-basis: 100%;
        margin: 10px;
    }
}
buttons__button:hover {
    color: rgb(10, 161, 161)
}
.buttons__button:disabled {
    color: #ccc;
}