.form {
    display: grid;
    grid-template-columns: 1fr auto;
    grid-gap: 20px;
}
@media (max-width: 767px) {
.form {
    grid-template-columns: 1fr;
}
}
.form__field {
    padding: 10px;
    border: 1px solid #ccc;
}
.form__button {
    padding: 10px;
    background-color: teal;
    color: white;
    border: none;
    transition: 1s;
}
.form__button:hover {
    background-color: rgb(12, 156, 156);
    transform: scale(1.1);
}
.form__button:active {
    background-color: rgb(8, 190, 190);
}
